.App {
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #333333;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
div#console-window{
  position: absolute;
    z-index: 1;
  width:30%;
  border:1px solid black;
  -webkit-box-shadow: 5px 10px #282828;
          box-shadow: 5px 10px #282828;
  border-radius: 5px;
}
div#console-header>*{
  color: white;
  display:contents;
}
div#console-header{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: white;
  background-color: rgb(38, 38, 38);
  border-bottom: 1px solid rgb(19, 19, 19);
  padding: 5px;
  padding-right: 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
div#console-display{
  color: lightgreen;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 5px;
  min-height: 100px;
  border-bottom: 1px solid black;
}
div#console-display div{
  color: inherit;
}
div#console-input{
  height: 32px;
  
}
div.console-typing{
  padding: 5px;
  color:white;
  background-color: #333333;
  text-align: left;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
div.console-typing >div {
  color: inherit;
}
div#user-Input{
  display:-ms-flexbox;
  display:flex;
  color:white;
}
div#input-Field{
  padding-top: 5px;
  padding-left: 5px;
  
  color:inherit;
}
div#nav-Container{
  background-color:red;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
}
div#info-Container{
  background-color:blue;
}
.blinking-cursor {
  /* font-weight: inherit; */
  padding-top:5px;
  font-size: large;
  -webkit-animation: 1s blink step-end infinite;
  animation: 1s blink step-end infinite;
}

@keyframes "blink" {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}

@-webkit-keyframes "blink" {
  from, to {
    color: transparent;
  }
  50% {
    color: inherit;
  }
}
.hideMe {
  /* Firefox */
  -webkit-animation: cssAnimation 0s ease-in 10s forwards;
  /* Safari and Chrome */
  /* Opera */
  animation: cssAnimation 0s ease-in 10s forwards;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

@keyframes cssAnimation {
  to {
      width:0;
      height:0;
      overflow:hidden;
  }
}
@-webkit-keyframes cssAnimation {
  to {
      width:0;
      height:0;
      visibility:hidden;
  }
}


